import './App.css';
import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { ethers } from 'ethers'
import Rewards from './artifacts/contracts/rewards.sol/RewardHolders.json'
//import Rewardsed from './whitlisted.json'
import listHolders from './addressesHoldersDiamondHands.json'
import holders from './rewardHoldersDiamondHands.json'
import Swal from 'sweetalert2'


require('dotenv').config();


//"0x9fA70129Fdd0f82122Ed32CEb0c298Bf0C9A8f92" // OTIUM - in mainnet of Binance Smart Chain
//const presaleaddress = "0x2Cc6c28D6632EC0938555fBe3aAC746101215FE5" // trusty Testnet 
const presaleaddress = "0x9B686775504DE24a29601F92C3bdDdE00AFC3aBd" // Contrato Reward


function App() {

  const { t, i18n } = useTranslation();
  const [network, setNetwork] = useState('no-net');
  const [contractBalance, setContractBalance] = useState(0);
  const [userAccount, setUserAccount] = useState('');
  const [userBalanceBNB, setUserBalanceBNB] = useState(0);
  const [userBalanceOtiums, setUserBalanceOtiums] = useState(0);
  const [amount, setAmount] = useState(0);
  const [claimStatus, setClaimStatus] = useState(false);
  const BINANCENETWORK = 'bnbt'; //Testnet
  //const BINANCENETWORK = 'bnb'; //MainNet

  //console.log('Despues de declarar las variables globales')
  //console.log('archivo Rewardsed.json', Rewardsed[0].address)
  //console.log('holders', holders)

  useEffect(function () {

    isconnectedMetamask();
    changeAccounts();
    changeNetwork();
    getBalance();
    
  },[],);

//===========================================================================================
  async function addNetwork() {

    let networkData = [{
            chainId: "0x61",
            chainName: "BSCTESTNET",
            rpcUrls: ["https://data-seed-prebsc-2-s3.binance.org:8545"],
            nativeCurrency: {
              name: "BINANCE COIN",
              symbol: "BNB",
              decimals: 18,
            },
            blockExplorerUrls: ["https://testnet.bscscan.com/"],
          },
        ];

      // let networkData = [{
      //     chainId: "0x38",
      //     chainName: "BSCMAINET",
      //     rpcUrls: ["https://bsc-dataseed1.binance.org"],
      //     nativeCurrency: {
      //       name: "BINANCE COIN",
      //       symbol: "BNB",
      //       decimals: 18,
      //     },
      //     blockExplorerUrls: ["https://testnet.bscscan.com/"],
      //   },
      // ];

      // agregar red o cambiar red
      return window.ethereum.request({
        method: "wallet_addEthereumChain",
        params: networkData,
      });
  }

//===========================================================================================
  useEffect(function () {
      //console.log('Dentro del useEffect network')
      if(network !== BINANCENETWORK && network !== 'no-net' ){ 
          checkNetwork();
        
        } else {
          //fetchrate();
          getBalance();
        }
          
      //console.log('useEffect de control de network. resultado', network );

  },[network]);


  function checkNetwork() {
    Swal.fire({
              title: t('red'),
              //text: `Cambia a BSC si la tienes o sigue el siguiente tutorial para configurarla`,
              text: t('cambia-red')+network,
              showCancelButton: true,
              confirmButtonText: t('btn-configurar-red'),
              //imageUrl: 'https://i2.wp.com/criptotendencia.com/wp-content/uploads/2020/04/binance-smart-chain.jpg?fit=1200%2C674&ssl=1',
              imageUrl: 'https://cryptodaily.io/wp-content/uploads/2021/07/p-2.png',
              imageWidth: 300,
              
              imageAlt: 'Red Binance Smart Chain',
            }).then((result) => {
              /* Read more about isConfirmed, isDenied below */
              if (result.isConfirmed) {
                addNetwork();
                //window.open('https://academy.binance.com/es/articles/connecting-metamask-to-binance-smart-chain', '_blank');  
              }
            })
  }


  if (typeof window.ethereum === 'undefined') {
  
    console.log('Metamask no existe en este navegador')

    Swal.fire({
      title:  t('no-metamask'),
      text: t('no-metamask-info'),
      showCancelButton: true,
      confirmButtonText: t('install-metamask'),
      imageUrl: './img/metamask-transparent.png',
      // imageWidth: 600,
      // imageHeight: 150,
      imageAlt: t('install-processing'),
    }).then((result) => {
      /* Read more about isConfirmed, isDenied below */
      if (result.isConfirmed) {
        window.open('https://chrome.google.com/webstore/detail/metamask/nkbihfbeogaeaoehlefnkodbefgpgknn', '_blank');  
      }
    })
  } 

  async function conectionMetamask() {
    //await isOwner();

    await window.ethereum.request({ method: 'eth_requestAccounts' });
    const provider = new ethers.providers.Web3Provider(window.ethereum);

    const network = await provider.getNetwork();
    console.log('network', network.name);
    setNetwork(network.name);

    const signer = provider.getSigner(); // user
    const accountUser = await signer.getAddress();
    console.log('cuenta usuario', accountUser)
     // setUserAccount(accountUser.toString());

    let cuentaUsuario = accountUser.toString();
    let subini = cuentaUsuario.substr(0,4);
    let subfinal = cuentaUsuario.substr(-4,4);
    document.querySelector('#wallet').innerHTML = `${subini} ... ${subfinal}`;

    const accountUserBalance = await provider.getBalance(accountUser);
    let amount = ethers.utils.formatEther(accountUserBalance).toString();
    let result = Number.parseFloat(amount).toFixed(2);
    const SYMBOL = "BNB";
    let resultado = result.concat(" ", SYMBOL);
    let bnbButton = document.querySelector('#bnbs');
    bnbButton.classList.remove('d-none');
    bnbButton.innerHTML = resultado;
    
    const contract = new ethers.Contract(presaleaddress, Rewards.abi, provider);
    try {
      const balanceOtiums = await contract.getBalanceOtium({ from: cuentaUsuario });
      let amountOtiums = ethers.utils.formatEther(balanceOtiums).toString();
      let otiums = Number.parseFloat(amountOtiums).toFixed(0);
      setUserBalanceOtiums(otiums);
      document.querySelector('#otiums').classList.remove('d-none');

    } catch (err) {
      console.log("Error: ", err)
    }


  }

  
  async function isconnectedMetamask() {
      //await isOwner();
      const accounts = await window.ethereum.request({ method: 'eth_accounts' });
      let cuentaUsuario = accounts[0];

      if (cuentaUsuario) {
        //!==========================================================================================================
        //console.log('cuentaUsuario en isConnectedMetamask', cuentaUsuario)
        //console.log('núm address rewards', listHolders.length)
        //console.log('num holders rewards', holders.length)
        const holderDiamondHands = holders.find(holder => holder.address === cuentaUsuario);
        //console.log('holderDiamondHands: ', holderDiamondHands);
        const holderDH = listHolders.find(holder => holder === cuentaUsuario);
        //console.log('holderDH en lista holders', holderDH);

        //!===========================================================================================================

        let subini = cuentaUsuario.substr(0,4);
        let subfinal = cuentaUsuario.substr(-4,4);
        //let cuentaRecortada = subini + '...' + subfinal;
        let cuentaRecortada = `${subini} ... ${subfinal}`;
        setUserAccount(cuentaUsuario);

        document.querySelector('#wallet').innerHTML = cuentaRecortada;

        const provider = new ethers.providers.Web3Provider(window.ethereum);
        const contractUserBalance = await provider.getBalance(cuentaUsuario);
        let amount = ethers.utils.formatEther(contractUserBalance).toString();
        let result = Number.parseFloat(amount).toFixed(2);
        let bnbButton = document.querySelector('#bnbs');
        bnbButton.classList.remove('d-none');
        bnbButton.innerHTML = `${result} BNB`;
        setUserBalanceBNB(`${result} BNB`);

        const network = await provider.getNetwork();
        //console.log('network', network.name);
        setNetwork(network.name);

        const contract = new ethers.Contract(presaleaddress, Rewards.abi, provider);
        try {
          const balanceOtiums = await contract.getBalanceOtium({ from: cuentaUsuario });
          let amountOtiums = ethers.utils.formatEther(balanceOtiums).toString();
          let otiums = Number.parseFloat(amountOtiums).toFixed(0);
          setUserBalanceOtiums(otiums);
          if (otiums > 0) {
            document.querySelector('#otiums').classList.remove('d-none');
          }

          const claimed = await contract.isClaimed(cuentaUsuario);
          console.log('ha reclamado? ', claimed)

          if (claimed) {
            setAmount(0);
          } else {
            
            if (holderDH) {
              let holderRw = holders.find(holder => holder.address === holderDH)
              setAmount(holderRw.reward.toFixed(4))
            } 
      
          }
          
        } catch (err) {
          console.log("Error: ", err)
        }
      
      }
  }
  
  // funcion que detecta los cambios de cuenta
  async function changeAccounts() {
  
    if (typeof window.ethereum !== 'undefined') {

      window.ethereum.on("accountsChanged", async function () {
        
        await isconnectedMetamask();
        
      });

    }
  }

   // funcion que detecta los cambios de red
  async function changeNetwork() {
  
    if (typeof window.ethereum !== 'undefined') {
    // detect Network account change
      window.ethereum.on('networkChanged', async function(networkId) {
        console.log('cambio de red efectuado..',networkId);
        await isconnectedMetamask();
      });

    }
  }


  async function getBalance() {

    if (typeof window.ethereum !== 'undefined') {
      //  const [account] = await window.ethereum.request({ method: 'eth_requestAccounts' });
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const contract = new ethers.Contract(presaleaddress, Rewards.abi, provider);

      try {
        const balance = await contract.TokenBalance();
        let tokens = ethers.utils.formatEther(balance).toString();
        let otiums = Number.parseFloat(tokens).toFixed(0);
        setContractBalance(otiums);

      } catch (err) {
        console.log("Error: ", err)
      }
    }
  }

  

  useEffect(function () {
    //console.log('useeffect de contracBalance');
    document.querySelector('#balance').innerHTML = contractBalance;

  },[contractBalance]);
  

  async function claimTokens() {

    if (typeof window.ethereum !== 'undefined') {

      if (network === BINANCENETWORK) {
        if (amount > 0) {
          
          const [account] = await window.ethereum.request({ method: 'eth_requestAccounts' });
          console.log('cuenta conectada: ', account);
          let holderRw = holders.find(holder => holder.address === account)
          console.log('holderRw:', holderRw);
          const provider = new ethers.providers.Web3Provider(window.ethereum);
          const signer = provider.getSigner();
          const contract = new ethers.Contract(presaleaddress, Rewards.abi, signer);

          //Llamamos a un evento del contrato para q esté a la escucha
          contract.on("RewardClaimed", (presaleaddress, account, amount) => {
            
            let tokens = ethers.utils.formatEther(amount).toString();
            let otiums = Number.parseFloat(tokens).toFixed(4);

            Swal.fire({
              title: t('congratulations'),
              text: t('have-claimed') + `${otiums} OTIUMS`,
              icon: 'success',
              confirmButtonText:  t('add-to-metamask'),
              confirmButtonClass: "addToMetaMask",
              buttonsStyling: false,
              showCloseButton: true,
            }).then((result) => {
              /* Read more about isConfirmed, isDenied below */
              if (result.isConfirmed) {
                //Sí queremos añadir el token a Metamask
                addTokenToMetamask();
              } 
            })

          })

       
          try {
          
            const weiAmount = ethers.utils.parseEther(holderRw.reward.toString());
            console.log('weiAmountReward', weiAmount, holderRw.reward)
            const transation = await contract.claimReward(weiAmount, {from:account});
            console.log("Esperando compraOk");
            Swal.fire({
              title: t('processing'),
              text: t('not-update'),
              // icon: 'info',
              showConfirmButton: false,
              imageUrl: 'https://thumbs.gfycat.com/ConventionalOblongFairybluebird-size_restricted.gif',
              imageWidth: 100,
              imageHeight: 100,
              imageAlt: t('purchase-processing'),

            })
            const compraOk = await transation.wait();

            if (compraOk) {
              document.getElementById('amount-otiums').value = 0;
          
              setAmount(0);
              isconnectedMetamask();
              getBalance();

            }

            console.log("Transation", transation);
            console.log("compraOk", compraOk);
            console.log(`La cuenta ${account}, ha recibido los Otiums correctamente.`);

          } catch (err) {
            let mensajeError = err.message;
            
            if (err.data) {

              if (err.data.message === 'execution reverted: Usted ya ha reclamado sus Otiums') {
                mensajeError =  t('already-claimed');
              } else {
                mensajeError =  t('transaction-error');
              }
            }
        
            Swal.fire({
              title: 'Ooops!',
              text: `${mensajeError}`,
              icon: 'error',
              confirmButtonText: 'Cerrar'
            })
            //console.log("Error: ", err)
          }
          
        }    
      
      } else {
        checkNetwork();
      }

    }
  }


  const addTokenToMetamask = async () => {

    if (typeof window.ethereum !== 'undefined') {
    
      //Otium REAL Token
      const tokenAddress = '0x56A02228CE17DBBbc809fe2262B36Dd99f28E824';
      const tokenSymbol = 'OTIUM';
      const tokenDecimals = 18;
      const tokenImage = 'https://gateway.pinata.cloud/ipfs/Qmcw8UcpyMiqzkESnZ9s8pmMfiE1Yn7rUwSVR2HWpDtgG7';

      // Deploy Otium Test Token
        // const tokenAddress = '0x86F108fb06E93f3Aa69b0F828e0333C250194719';
        // const tokenSymbol = 'DOT';
        // const tokenDecimals = 18;
        // const tokenImage = 'https://upload.wikimedia.org/wikipedia/commons/thumb/f/f7/Antu_dialog-warning.svg/512px-Antu_dialog-warning.svg.png';
      
      try {
        // wasAdded is a boolean. Like any RPC method, an error may be thrown.
        const wasAdded = await window.ethereum.request({
          method: 'wallet_watchAsset',
          params: {
            type: 'ERC20', // Initially only supports ERC20, but eventually more!
            options: {
              address: tokenAddress, // The address that the token is at.
              symbol: tokenSymbol, // A ticker symbol or shorthand, up to 5 chars.
              decimals: tokenDecimals, // The number of decimals in the token
              image: tokenImage, // A string url of the token logo
            },
          },
        });

      } catch (error) {
        console.log(error);
      }
    }

  }
  

  return (
    <div className="App">
      <main className="d-lg-none mt-5">
        <div className="my-5">
          <a className="navbar-brand" href="https://otiumtechnologies.com/es/">
            <img src="./img/logo.png" height="30" alt="Logo de Otium" />
          </a>
        </div>
        <div className="my-5">
          <h2 className="text-white my-5">The sale is only enabled through a computer</h2>
          <h2 className="my-5">La venta solo es posible conectándose con un Ordenador</h2>
        </div>
      </main>
      <main className="d-none d-lg-block">
        <header className="mb-5">
          <nav className="d-flex justify-content-between align-items-center mx-3 my-3">
            <div>
              <a className="navbar-brand" href="https://otiumtechnologies.com/es/">
                <img src="./img/LOGO_Letras_BLANCAS.png" height="70" alt="Logo de Otium" />
              </a>
            </div>
            <div>
              <button id="wallet" className="connectionMM btnHeader" onClick={conectionMetamask}>{t('conectarMM')}</button>
              <button id="bnbs" className="BNB btnHeader mx-2 d-none">BNBs</button>
              <button id="otiums" className="otium btnHeader d-none"> {userBalanceOtiums} Otiums</button>
              {(i18n.language == "en") ? <a className="col-md-2" onClick={() => i18n.changeLanguage('es')}><img src="./img/es.png" alt="Español" /></a> : <a className="col-md-2" onClick={() => i18n.changeLanguage('en')}><img src="./img/en.png" alt="English" /></a>}
            </div>
          </nav>
        </header>
      
        <div className="App-buttons my-3">
          <div className="row mt-3 mt-md-0 mb-3 mx-3 mx-md-0">
            <div className="card-deck">
              <div className="card pt-4 pb-3 otiumCard">
                <h2> <b>Otium</b> {t('otium-reward')}</h2>
                <div className="card-body">
                  <h3 className="card-title numberSize" id="balance">...</h3>
                  <p className="card-text">{t('otium-disponibles')}</p>  
                </div>
              </div>
            </div>
          </div>
          
          <div className="row">
            <div className="col-12 mb-5">
              <div id="infoBuyAllowed" className="d-flex align-items-center justify-content-center">
                <div id="rewardDates" className="display-5 text-white">{t('reward-date')}</div>
              </div>
              <div className="form-wrapper">
                <label className="font-weight-bold otiumColor"><img className="mr-2 mb-2" src="./img/mini-logo_otium.png" alt="Logo Otium" />OTIUM {t('input-info')}</label>
                <input className="text-center amount" id="amount-otiums" value={(amount === 0)? "0" : amount} disabled />
              </div>

              <button className="buy font-weight-bold" onClick={claimTokens} >{t('boton-reclamar')} <b>Otiums</b></button>

            </div>
          </div>
         
        </div>
          <div>
          <a href="https://otiumtechnologies.com/es/">
            <img className="logoBlanco" src="./img/LOGO_OTIUM_blanco.png" alt="logo Otium" />
          </a>
        </div>
      </main>
      
    </div>
  );
  
}

export default App;
